import React from 'react'
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardHeader from '@mui/material/CardHeader';
import { CardMedia, useMediaQuery } from '@mui/material';
import Typography from '@mui/material/Typography';
import { createTheme, ThemeProvider } from '@mui/material/styles';


export function SimpleCard({ title = 'Titulo de la card', content = 'Contenido de la card', background_color = '#4e4c6b' }) {
    const card_theme = createTheme({
        components: {
            // Name of the component
            MuiCard: {
                styleOverrides: {
                    // Name of the slot
                    root: {
                        // Some CSS
                        margin: '0 1rem 0 1rem',
                        background: background_color,
                        color: 'white'
                    },
                },
            },
        },
    });
    const query400px = useMediaQuery('(min-width:400px)');
    let cards_style = {
        minWidth: 250,
        maxWidth: 350.4,
    }
    if (!query400px) {
        cards_style['marginTop'] = '10px'
        cards_style['marginBottom'] = '10px'
    }
    else {
        cards_style['height'] = '100%'
    }


    return (
        <ThemeProvider theme={card_theme}>
            <Card variant="outlined" sx={cards_style}>
                <CardHeader title={title} titleTypographyProps={{ fontFamily: "'Fraunces', serif", }}></CardHeader>
                <CardContent>
                    <Typography variant="body2">
                        {content}
                    </Typography>
                </CardContent>
            </Card>
        </ThemeProvider>
    )
}



export function ImageCard({ title = 'Titulo de la card', content = 'Contenido de la card', image = 'none' }) {
    const card_theme = createTheme({
        components: {
            // Name of the component
            MuiCard: {
                styleOverrides: {
                    // Name of the slot
                    root: {
                        // Some CSS
                        margin: '0 1rem 0 1rem',
                        background: 'transparent',
                        color: 'white'
                    },
                },
            },
        },
    });


    return (
        <ThemeProvider theme={card_theme}>
            <Card variant="outlined" sx={{
                minWidth: 250,
                maxWidth: 500,
                marginTop: '10px',
                marginBottom: '10px',
            }}>
                <CardMedia
                    sx={{
                        aspectRatio: 5 / 3,
                        borderRadius: '5px',
                        objectFit: 'cover',
                        objectPosition: 'center center',
                    }}
                    image={image}
                    title={title}
                />
                <CardHeader title={title} titleTypographyProps={{ fontFamily: "'Fraunces', serif", }}></CardHeader>
                <CardContent>
                    <Typography variant="body2">
                        {content}
                    </Typography>
                </CardContent>
            </Card>
        </ThemeProvider>
    )
}