import React from 'react'
import { Box, Container } from '@mui/material'

export function ClientsSection() {
  return (
    <Box sx={{
        padding: '5rem 0 5rem 0',
    }}>
        <Container>
            <div>
                <h1>Clientes Satisfechos</h1>
                <p>En NC, nos tomamos muy en serio la satisfaccin de nuestros clientes. Hemos trabajado con varias empresas y organizaciones de diferentes tamaos y en diversos pases, y siempre hemos logrado superar las expectativas.</p>
            </div>
        </Container>
    </Box>
  )
}
