import React, { useEffect } from 'react';
import { Home } from './Home';
import './assets/css/styles.css'

import AOS from 'aos';
import 'aos/dist/aos.css';

function remove_aos_delay() {
    if (window.screen.width <= 400) {
        for (let elem of document.getElementsByClassName('aos-init')) {
            elem.removeAttribute('data-aos-delay')
        }
    }
}


export function App() {
    const BrandName = 'NC'
    useEffect(() => {
        AOS.init();
        remove_aos_delay();
    }, [])

    return (
        <>
                <Home brand_name={BrandName} />
        </>
    )


}