import React from 'react'
import { Container, useMediaQuery } from '@mui/material'
import Box from '@mui/material/Box';
import { SimpleCard } from './Cards'

export function MarketingSection() {
    const query400px = useMediaQuery('(min-width:400px)');
    const query900px = useMediaQuery('(min-width:900px)');
    let cards_container_style = {
        display: 'flex',
        justifyContent: 'center',
    }
    if (!query400px) {
        cards_container_style['flexDirection'] = 'column'
    }
    else {
        cards_container_style['flexDirection'] = 'row'
    }

    if (!query900px) {
        cards_container_style['flexWrap'] = 'wrap'
    }
    else {
        cards_container_style['flexWrap'] = 'nowrap'
    }

    return (
        <Box sx={{
            padding: '5rem 0 5rem 0',
        }}>
            <Container>
                <div className='aos' data-aos="zoom-in" data-aos-delay="200" data-aos-duration="1000">
                    <h1>Marketing Digital</h1>
                    <p>Nuestra especialidad es el marketing digital, ofrecemos soluciones personalizadas para ayudarte a alcanzar tus objetivos. Desde SEO y SEM hasta redes sociales y publicidad en lnea, ponemos el poder del marketing digital a tu alcance</p>
                </div>
                <Box sx={cards_container_style}>
                    <div className='aos' data-aos="zoom-in" data-aos-delay="200" data-aos-duration="1000">
                        <SimpleCard title='SEO y SEM' content='Optimizacin para motores de bsqueda y estrategias de publicidad para lograr mayor visibilidad en lnea.' />
                    </div>
                    <div className='aos' data-aos="zoom-in" data-aos-delay="400" data-aos-duration="1000">
                        <SimpleCard title='Redes Sociales' content='Estrategias efectivas de gestin de redes sociales para conectar con audiencias especficas.' />
                    </div>
                    <div className='aos' data-aos="zoom-in" data-aos-delay="600" data-aos-duration="1000">
                        <SimpleCard title='Publicidad en lnea' content='La publicidad en lnea ms efectiva para aumentar el alcance y generar conversiones.' />
                    </div>
                </Box>
            </Container>
        </Box>
    )
}